        <li><a href="#/eduAdmin/markManage/adminCancelGrade">管理员撤销成绩-李永建（待完善）</a></li>
        <li><a href="#/eduAdmin/markManage/gradeQuery">成绩查询（学号）-李永建（已对接）</a></li>
        <li><a href="#/eduAdmin/markManage/gradeStat">成绩（分数段）-李永建（已对接）</a></li>
        <li><a href="#/eduAdmin/markManage/makeupExamAdmin">补考成绩-李永建（已写）</a></li>
        <li><a href="#/eduAdmin/tchSalary/salary">薪酬单-程奕茗（已写）</a></li>
        <li><a href="#/eduAdmin/tchSalary/setPrice">设置单价-张文元（已对接）</a></li>
        <li><a href="#/eduAdmin/userManage/authorityMgmt1">权限管理1-郑伟（已对接）</a></li>
        <li><a href="#/teacher/tchCheckTimetable">教师查看课表-张孟涵（已对接）</a></li>
        <li><a href="#/teacher/courseList">上课列表（签到）-程奕茗（已写）</a></li>
        <li><a href="#/teacher/gradesInput">成绩输入-李永建（已写）</a></li>
        <li><a href="#/teacher/tchGradesInput">教师成绩输入-李永建（已写）</a></li>
        <li><a href="#/teacher/tchManuAdjCl">手动调课-徐佳俊（已对接）</a></li>
      </ul>

      <!-- 有标题栏的弹出框 -->
